import { useUser } from "@clerk/clerk-react"
import { useMutation, useQueryClient } from "@tanstack/react-query"
import { deleteMany } from "../services"
import { handleDeleteFilesSuccess } from "../utils/handleDeleteFilesSuccess"

const useDeleteFiles = () => {
  const queryClient = useQueryClient()
  const { user } = useUser()

  return useMutation({
    mutationFn: deleteMany,
    onSuccess: async (_response, fileIds) => handleDeleteFilesSuccess(fileIds, user, queryClient),
  })
}

export { useDeleteFiles }
